import React, { Component } from "react"
import PacMan from "../../loader/pacMan"
import axios from "axios"
import { config } from "../../../config/Settings"

class DownloadFinished extends Component {
  constructor(props) {
    super(props)
    this.aibox_id = this.props.params.aibox_id
    this.state = {
      data: null,
      loading: true,
      error: null,
    }
    this.getDownloadFinished = this.getDownloadFinished.bind(this)
  }

  componentDidMount() {
    this.getDownloadFinished()

    this.updateMetric = setInterval(
      this.getDownloadFinished,
      config.updateInterval
    )
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric)
  }

  getDownloadFinished() {
    axios
      .get(
        `https://spi.cavea.io/ai/que/count?filter=%7B%22state%22:%20%22download-finished%22,%22aibox_id%22:%22${this.aibox_id}%22%7D&limit=9999999`
      )
      .then((response) => {
        this.setState({ data: response.data || "0", loading: false, error: false })
      })
      .catch((e) => {
        return this.setState({ error: e, loading: false })
      })
  }

  render() {  
    if (this.state.data && !this.state.error && !this.state.loading) {
      return (
        <code className="text-center text-gray-900 dark:text-gray-200 flex-col content-end subpixel-antialiased">
        <p> Dl-finished:</p> <p> {this.state.data} </p>
      </code>
      )
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
          An Error Occurred!
        </p>
      )
    } else {
      return <PacMan />
    }
  }
}

export default DownloadFinished
