import React, { useState } from "react"
import Averages from "../text/importers/Averages"
import LineGraph from "./importers/LineGraph"
import Runtimes from "../text/importers/Runtimes"

function ImportGraphs(props) {
  const [graphs] = useState([...props.graphs])

  return graphs.map((graph, index) => {
    return (
      <div
        className="metric grid grid-cols-9"
        key={graph.platform + graph.header + index}
      >
        <div className="metric-header grid grid-flow-row-dense auto-rows-min col-span-2">
          <div className="grid grid-flow-col items-center">
            <h2 className="text-left font-semibold text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
              {graph.header}
            </h2>
            <Runtimes data={graph.runtime} platform={props.platform} />
          </div>
          <Averages data={graph.averages} platform={props.platform} />
        </div>
        <div className="col-span-7 grid-flow-row-dense">
          <LineGraph
            platform={props.platform}
            color={props.color}
            type={graph.total.type}
            width={200}
            height={120}
          />
        </div>
      </div>
    )
  })
}

export default ImportGraphs
