import ImportMetrics from "./components/ImportMetrics"
import AIMetrics from "./components/AIMetrics"
import  AiTypes  from "./components/AiTypes"
import { importers } from "./config/Importers"
import { AI } from "./config/AI"
import { type } from "./config/typeOfContent"
import AIStarted from "./components/text/ai/AIStarted"
import DownloadPending from "./components/text/ai/DownloadPending"

const loadImporterMetrics = importers.map((platform) => {
  return <ImportMetrics {...platform} key={platform.platform} />
})
const loadAIMetrics = AI.map((ai) => {
  return <AIMetrics {...ai} key={ai.boxName} />
})

const loadTypes = type.map((type) => {
  return <AiTypes {...type} key={type.boxName} />
})

const App = () => (
  <div className="p-5 mx-auto grid md:grid-cols-1 lg:grid-cols-2 gap-x-5">
    <div className="import-logs">{loadImporterMetrics}</div>
    <div className="ai-logs">
      <div className="grid grid-cols-2 grid-flow-col">
        <DownloadPending />
        <AIStarted />
      </div>
      {loadAIMetrics}
      {loadTypes}
    </div>
  </div>
)

export default App
