import React from "react"
import ImportGraphs from "./graphs/ImportGraphs"

function ImportMetrics(props) {
  return (
    <div id={props.platform + "-metric"} className="grid">
      <h2 className="capitalize text-left font-bold text-3xl text-gray-900 dark:text-gray-200 subpixel-antialiased">
        {props.platform} 
      </h2>
      <ImportGraphs
        graphs={props.graphs}
        platform={props.platform}
        color={props.color}
      />
    </div>
  )
}

export default ImportMetrics
