import React, { Component } from "react"
import PacMan from "../../loader/pacMan"
import axios from "axios"
import {
  Bar,
  BarChart,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from "recharts"
import { config } from "../../../config/Settings"

class AIDownloadFailed extends Component {
  constructor(props) {
    super(props)
    this.boxName = this.props.boxName
    this.params = this.props.params
    this.state = {
      data: null,
      loading: true,
      error: null,
    }
    this.getLogs = this.getLogs.bind(this)
  }

  componentDidMount() {
    this.getLogs()

    this.updateMetric = setInterval(this.getLogs, config.updateInterval)
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric)
  }

  getLogs() {
    axios
      .get(`${config.logUrl}/api/logs/ai/get_downloads`, {
        params: this.params,
      })
      .then((response) => {
        this.setState({ data: response.data, loading: false, error: false })
      })
      .catch(() => {
        return this.setState({ error: true, loading: false })
      })
  }
  
  render() {
    if (this.state.data && !this.state.error && !this.state.loading) {
      return (
        <ResponsiveContainer width="100%" height={config.graphHeight}>
          <BarChart data={this.state.data}>
            <XAxis dataKey="date" stroke="#222" tick={false} />
            <YAxis />
            <Tooltip />
            <Bar
              dataKey="aibox1"
              name="aibox1"
              stackId="a"
              fill={this.props.colors}
            />
            <Bar
              dataKey="aibox2"
              name="aibox2"
              stackId="a"
              fill={this.props.colors}
            />
            <Bar
              dataKey="aibox3"
              name="aibox3"
              stackId="a"
              fill={this.props.colors}
            />
            <Bar
              dataKey="aibox4"
              name="aibox4"
              stackId="a"
              fill={this.props.colors}
            />
          </BarChart>
        </ResponsiveContainer>
      )
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200">
          An Error Occurred!
        </p>
      )
    } else {
      return <PacMan />
    }
  }
}

export default AIDownloadFailed
