import React, { Component } from "react"
import PacMan from "../../loader/pacMan"
import axios from "axios"
import { config } from "../../../config/Settings"

class DownloadPending extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: null,
      loading: true,
      error: null,
    }
    this.getDownloadPending = this.getDownloadPending.bind(this)
  }

  componentDidMount() {
    this.getDownloadPending()

    this.updateMetric = setInterval(
      this.getDownloadPending,
      config.updateInterval
    )
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric)
  }

  getDownloadPending() {
    axios
      .get(
        `https://spi.cavea.io/ai/que/count?filter={%22state%22:%20%22download-pending%22}&limit=9999999`
        
      )
      .then((response) => {
        this.setState({ data: response.data || "0", loading: false, error: false })
      })
      .catch((e) => {
        return this.setState({ error: e, loading: false })
      })
  }

  render() {
    if (this.state.data && !this.state.error && !this.state.loading) {
      return (
        <h1 className="text-center text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
          Num. Pending Items: {this.state.data}
        </h1>
      )
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
          An Error Occurred!
        </p>
      )
    } else {
      return <PacMan />
    }
  }
}

export default DownloadPending
